import React, { useState } from "react";
import styled from "styled-components";
import { useParams } from "react-router-dom";
import projectData from "../config/data/projectData.json";
import { Swiper, SwiperSlide } from "swiper/react";

function ProjectDetail() {
  const params = useParams();
  const post = projectData.find((item) => {
    return item.num == params.no;
  });

  return (
    <StyledProjectDetail>
      <div className="detailWrap">
        <h2>{post.name}</h2>
        <p>{post.description}</p>
        <p>{post.worker}</p>
        <p>{post.skill}</p>
        <p>{post.etc}</p>
        <p>{post.function}</p>

        <Swiper slidesPerView={1}>
          <SwiperSlide>
            <img
              src={process.env.PUBLIC_URL + "/project_image/" + post.img_addr1}
            ></img>
          </SwiperSlide>
        </Swiper>
      </div>
    </StyledProjectDetail>
  );
}

const StyledProjectDetail = styled.div`
  height: 100%;
  width: 100%;
  margin: auto;
  padding-top: 200px;
  padding-bottom: 100px;
  background: #222;

  .detailWrap {
    width: 1100px;
    height: 100%;
    margin: auto;
    color: #fff;

    h2 {
      cursor: pointer;
      text-align: center;
      font-weight: 500;
      font-size: 2.5rem;
      font-family: "Montserrat";
      margin: 20px 0 150px 0;
    }
  }
`;

export default ProjectDetail;
